.about__container {
  display: grid;
  grid-template-columns: 100% 100%;
  gap: 15%;
  text-align: center;
}

.about__me {
  width: 100%;
  aspect-ratio: 1/1;
  border-radius: 2rem;
  background: linear-gradient(
    45deg,
    transparent,
    var(--color-primary),
    transparent
  );
  display: grid;
  place-items: center;
}

.about__cards {
  display: grid;
  grid-template-columns: repeat(4, 1fr);
  gap: 1.5rem;
}

.about__card {
  z-index: 1;
  background: var(--color-bg-variant);
  border: 1px solid transparent;
  border-radius: 1rem;
  padding: 2rem;
  text-align: center;
  transition: var(--transition);
  margin-top: 2rem;
}

.about__card:hover {
  background: transparent;
  border-color: var(--color-primary-variant);
  cursor: default;
  transition: 1s ease-in-out;
}

.about__icon {
  color: var(--color-primary);
  font-size: 1.4rem;
}

.about__card h5 {
  font-size: 0.95rem;
}

.about__card small {
  font-size: 0.7rem;
  color: var(--color-light);
}

.about__content p {
  margin: 2rem 0 2.6rem;
  color: var(--color-light);
  margin-top: 0;
}

.about__content {
  z-index: 1;
}

#about {
  height: 70vh;
}

.contact_button {
  margin-top: 4rem;
}

/*********** MEDIA QUERIES (MEDIUM DEVICES ************/
@media screen and (max-width: 1024px) {
  .about__cards {
    grid-template-columns: repeat(2, 1fr);
    gap: 0.5rem;
  }
  .about__me {
    width: 50%;
    margin: 2rem auto 4rem;
  }
  .about__content p {
    margin: 1rem 0 1.5rem;
  }
  .contact_button {
    display: none;
  }
  #about {
    height: 120vh;
  }
}

/*********** MEDIA QUERIES (SMALL DEVICES ************/
@media screen and (max-width: 600px) {
  .about__me {
    width: 65%;
    margin: 0 auto 3rem;
  }

  .about__cards {
    grid-template-columns: repeat(1, 1fr);
    gap: 0.5rem;
  }

  .about__content {
    text-align: center;
  }

  .about__content p {
    margin: 1.5rem 0;
  }
  .texto {
    font-size: small;
  }
  #about {
    height: 160vh;
  }
  .contact_button {
    display: none;
  }
}
